@import "/src/extention";

.main {
  display: flex;
  flex-direction: column;
  align-items: center;

  .banner {
    margin: 0 2%;
    width: 96%;
  }

  .cars {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 70vw;
  }

  .marketing {
    width: 70vw;

  }

  .about {
    width: 50vw;
    text-align: center;

    img {
      width: 100%;
    }
  }

}


.arrow {
  z-index: 100;
  width: 2vw;
  height: 4vh;
  position: relative;
  background: #FFF;
  top: -4vh;
  border-radius: 6px;

  &:hover {
    background: $mainRed;

    svg {
      color: #eeeeee;
    }
  }

  svg {
    width: 2vw;
    height: 2vh;
    position: relative;
    color: black;
  }
}

.arrowLeft {
  left: 90%;
}

.arrowRight {
  left: -4%;
}

.sliderWrap {
  display: flex;
  margin-top: 5vh;
  justify-content: space-between;

  img {
    width: 30%;
    height: 40%;
    animation: 500ms ease-in fadeIn;
  }

  margin-bottom: 7vh;
}

.banks {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  img {
    width: 250px;
    margin-right: 30px;
    margin-bottom: 30px;
  }
}


@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) {
  .main {
    .cars {
      width: 95vw;
    }
  }

  .sliderWrap {
    img {
      width: 50%;
      height: 40%;
    }
  }

  .arrowLeft {
    left: 93%;
  }

  .arrow {
    width: 3vw;

    svg {
      width: 3vw;
    }
  }

  .banks {
    width: 78vw;
    justify-content: space-evenly;
  }


}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {

  .main {
    .about {
      width: 90vw;
    }

    .cars {
      width: 85vw;
      justify-content: center;
    }
  }

  .sliderWrap {
    img {
      width: 85%;
    }

  }
  .arrowLeft {
    left: 78%;
  }
  .arrow {
    width: 6vw;

    svg {
      width: 6vw;
    }
  }
  .banks {
    img {
      margin-right: 0;
    }
  }
}


