@import "/src/extention";

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;

  .title {
    width: 80%;
    margin: 0;
    font-size: 2rem;

    .name {
      font-weight: bold;
    }
  }

  .specifications {
    margin-top: 5vh;
    display: grid;
    grid-template-columns: 20% 80%;
    width: 90%;
    padding-left: 7%;
    border-bottom: 1px solid rgba(151, 151, 151, 0.2);
    font-size: 14px;

    &:last-of-type {
      border-bottom: none;
    }

    .elements {
      padding-top: 2.3vh;
      display: flex;
      flex-wrap: wrap;

      p {
        margin-right: 3%;
        margin-top: 0.7vh;
        width: 20%;
      }
    }
  }

}

img {
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.arrow {
  z-index: 100;
  width: 2vw;
  position: relative;

  &:hover {
    background: rgba(0, 0, 0, 0.3);
  }

  svg {
    width: 2vw;
    height: 2vh;
    position: relative;
    top: 45%;
    color: #eeeeee;
  }
}

.arrowLeft {
  left: 4%;
}

.arrowRight {
  left: -4%;
}

.sliderWrap {
  display: flex;
  width: 100%;
  margin-top: 5vh;
  justify-content: space-between;
  height: 60vh;

  img {
    width: 45%;
    animation: 500ms ease-in fadeIn;
  }
}

.mainImg {
  width: 45%;
}

.options {

}


@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) {
  .wrapper {
    .specifications {
      grid-template-columns: 30% 70%;

      .elements {
        p {
          width: 45%;
        }
      }
    }
  }


  .sliderWrap {
    height: 40vh;
  }
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .wrapper {
    .specifications {
      grid-template-columns: 100%;

      .elements {
        p {
          width: 90%;
          display: grid;
          grid-template-columns: 40% 40%;
          grid-column-gap: 20%;
        }
      }

    }

    .options {
      .elements {
        p {
          display: inline;
        }
      }

    }

  }

  .sliderWrap {
    height: 40vh;

    img {
      width: 90%;
    }
  }

  .arrow {
    width: 10vw;

    svg {
      width: 4vw;
      height: 4vh;
    }
  }


  .arrowLeft {
    left: 5%;
  }

  .arrowRight {
    left: -5%;
  }


}
